import Toast from '../../wxcomponents/vant/dist/toast/toast';
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	
	data() {
		return {
			fileList:[],
			imgArr:[],
			name:'',
			tag_id:'',
			country:'',
			province:'',
			city:'',
			content1:'',
			content2:'',
			birthday:"",
			cname:'',
			duty:'',
			dutyList:["董事长", "总经理", "总经理秘书", "总裁", "副总裁", "总裁助理", "总裁办主任", "人力资源总监", "销售总监", "技术总监", "财务总监", "生产总监", "会计", "出纳", "人事经理", "人事助理", "销售经理", "采购经理"],
			email:'',
			resume:'',
			finish:false
			
		}
	},
	watch: {
	    finish () {
	      if (this.finish) {
	        uni.hideLoading();
	        this.$api.create_city_v2({
	          token: uni.getStorageSync("token"),
	          user_id: uni.getStorageSync("userId"),
	          name:this.name,
			  tag_id:this.tag_id,
			  country:this.country,
			  province:this.province,
			  city:this.city,
			  description:this.content2,
			  birthday:this.birthday,
			  email:this.email,
			  duty:this.cname+this.duty,
			  imagine:this.content1,
			  resume:this.resume,
			  images:this.imgArr
	        }).then(res => {
				uni.showToast({
				    title: res.data.msg,
				    mask: "true",
					icon:'none'
				});
	          if (res.data.code === '200') {
				  setTimeout(function() {
					  uni.reLaunch({
					  	url:'../../pagesTwo/CreateClubDetail/CreateClubDetail'
					  })
				  }, 1000);
	          }
	        })
	      }
	    }
	},
	onLoad() {
		uni.$on('createClubOne',(data)=>{
		   this.name = data.name
		   this.tag_id = data.tag_id
		   this.country = data.country
		   this.province = data.province
		   this.city = data.city?data.city:''
		   this.content1 = data.content1
		   this.content2 = data.content2
		   // console.log(this.name)
		   // console.log(this.tag_id)
		   // console.log(this.country)
		   // console.log(this.province)
		   // console.log(this.city)
		   // console.log(this.content1)
		   // console.log(this.content2)
		})
		// this.getData()
	},
	onUnload() {
	     uni.$off('createClubOne');  
	},
	methods: {
		pickDuty(e){
			this.duty = this.dutyList[e.detail.value]
		},
		pickBir(e){
			this.birthday = e.detail.value
			
		},
		send () {
				var that = this
		        if (this.birthday && this.cname && this.duty && this.email && this.resume && this.fileList.length>0) {
					uni.showLoading({
					    title: '加载中',
						mask:"true"
					});
					uni.uploadFile({
							  url: app.aliUrl,
							  filePath: that.fileList[0].url,
							  name: 'file',
							  formData: {
								  key:"wx/"+"${filename}",
								  signature: app.signature,
								  policy:app.policy,
								  OSSAccessKeyId:app.OSSAccessKeyId,
								  success_action_status:'201'
							  }
					 }).then(res=>{
						 
						 that.imgArr.push({
							 hash:'',
							 url_image:res[1].data.split('<Location>')[1].split("</Location>")[0],
							 size:''
						 })
						 
						 if(that.imgArr.length>0){
							 that.imgArr = JSON.stringify(that.imgArr).split('[')[1].split(']')[0]
							 that.finish = true
						 }
					 })
					  
					
						
		        } else {
		          if (!this.birthday) {
					Toast('请选择生日')
		            return
		          }
		          if (!this.cname) {
					Toast('公司没有填写')
		            return
		          }
				  if (!this.duty) {
				  	Toast('请选择职务')
				    return
				  }
		          if (!this.email) {
					Toast('请填写邮箱')
					return
		          }
				  if (!this.resume) {
					Toast('请填写个人简历')
					return
				  }
				  if (this.fileList.length==0) {
					Toast('请上传身份证/绿卡/护照')
					return
				  }
		       }
		},
		// 删除图片
		del(e){
			this.fileList = this.fileList.filter((item,index)=> index!=e.detail.index)
		},
		// 图片上传后的预览
		afterRead(event) {
		      const { file } = event.detail;
		      // 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式
		      this.fileList.push({url:file[0].path})
			  setTimeout(()=>{
				  uni.pageScrollTo({
				  	scrollTop:1000
				  })
			  }, 300);
		}
	}
}
